
import XHeader from 'vux/src/components/x-header/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import Popup from 'vux/src/components/popup/index.vue'

export default {
name:'partner',
data () {
return {
vm:{
visible:false
},
currentPage:-1,
pageSize:15,
list:[]
}
},
components: {
XHeader,
Spinner,
Popup
},
created () {

},
methods:{
query(param){
this.$http.post({
url:'/my/work',
data:{
userId:sessionStorage.userId,
currentPage:this.currentPage,
pageSize:this.pageSize
},
type:"json",
success: data => {
if(data.length > 0){
if(param && param.done){
setTimeout(_ => {
if(param.type == 1){
this.list = this.list.concat(data);
}else if(param.type == 2){
this.list = data;
}
param.done();
},1500)
}else{
this.list = data;
}
}else{
this.$refs.myScroller.finishInfinite(2);
}
},
error:msg => {
this.$vux.toast.text(msg, 'top');
}
})
},
loadMore(done){
this.currentPage = this.currentPage + 1;
this.query({type:1,done:done});
},
refresh(done){
this.currentPage = 0;
this.query({type:2,done:done});
}
}
}



